(() => {
  // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â–∏–π –ø—É—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—ã (–±–µ–∑ —Å–ª–µ—à–∞ –≤ –∫–æ–Ω—Ü–µ)
  const currentPath = window.location.pathname.replace(/\/$/, '') || '/';

  // –í—Å–µ —Å—Å—ã–ª–∫–∏ –≤ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ —Ö–µ–¥–µ—Ä–∞
  const navLinks = document.querySelectorAll('.header-link'); // –∏–ª–∏ '.header-list a'

  navLinks.forEach(link => {
    // –£–±–∏—Ä–∞–µ–º –∫–ª–∞—Å—Å current —É –≤—Å–µ—Ö
    link.classList.remove('current');

    // –ü–æ–ª—É—á–∞–µ–º –ø—É—Ç—å —Å—Å—ã–ª–∫–∏
    const linkPath = new URL(link.href).pathname.replace(/\/$/, '') || '/';

    // –ï—Å–ª–∏ –ø—É—Ç—å —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å —Ç–µ–∫—É—â–∏–º ‚Äî –¥–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å current
    if (linkPath === currentPath) {
      link.classList.add('current');
    }
  });
})();
