// contact-form.js ‚Äî —Ñ–æ—Ä–º–∞ "Get in touch with us"  pricing/enterprise
// contact-form.js ‚Äî —Ñ–æ—Ä–º–∞ "Get in touch with us"  customer/work
const contactForm = document.getElementById('contact-form');
const contactMessage = document.getElementById('contact-message');

if (contactForm) {
  contactForm.addEventListener('submit', e => {
    e.preventDefault();

    const name = contactForm.name.value.trim();
    const email = contactForm.email.value.trim();

    contactMessage.textContent = '';
    contactMessage.style.color = '';

    if (!name || !email) {
      contactMessage.textContent = '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –∏–º—è –∏ email';
      contactMessage.style.color = '#d93025';
      return;
    }

    // –£—Å–ø–µ—Ö ‚Äî —Å–æ–æ–±—â–µ–Ω–∏–µ –≤–Ω—É—Ç—Ä–∏ –º–æ–¥–∞–ª–∫–∏
    contactMessage.textContent =
      '–°–ø–∞—Å–∏–±–æ! –ù–∞—à —ç–∫—Å–ø–µ—Ä—Ç —Å–≤—è–∂–µ—Ç—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.';
    contactMessage.style.color = '#02897a';

    // –û—á–∏—â–∞–µ–º —Ñ–æ—Ä–º—É
    contactForm.reset();

    // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª–∫—É —á–µ—Ä–µ–∑ 2 —Å–µ–∫—É–Ω–¥—ã
    setTimeout(() => {
      const backdrop = document.querySelector('[data-modal="contact"]');
      if (backdrop) {
        backdrop.classList.remove('is-modal-open');
        document.body.style.overflow = '';
      }
      contactMessage.textContent = '';
    }, 2000);
  });
}
