import { openModalById } from './modal-core.js';

const LOGIN_FLAG = 'isLoggedIn';

document.querySelectorAll('.pricing-btn').forEach(btn => {
  btn.addEventListener('click', () => {
    const plan = btn.getAttribute('data-plan') || '–≤—ã–±—Ä–∞–Ω–Ω—ã–π —Ç–∞—Ä–∏—Ñ';

    const logged = localStorage.getItem(LOGIN_FLAG) === 'true';

    if (logged) {
      alert(
        `–°–ø–∞—Å–∏–±–æ –∑–∞ –∑–∞–∫–∞–∑ —Ç–∞—Ä–∏—Ñ–∞ "${plan}"! –ù–∞—à –º–µ–Ω–µ–¥–∂–µ—Ä —Å–≤—è–∂–µ—Ç—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.`
      );
      return;
    }

    sessionStorage.setItem('pendingPlan', plan);

    openModalById('signup');
  });
});
